import React from "react";
import { BrowserRouter, Link } from "react-router-dom";

function Thumbnail(props) {
    return (
        <div className="project" data-category={props.dataCat}>
            <BrowserRouter>
                <Link
                    to={{
                        pathname: props.link,
                    }}
                    target="_blank"
                >
                    <img
                        className="image"
                        src={props.image}
                        alt={props.title}
                    />
                    <div className="inner">
                        <props.icon />

                        <div>
                            <span className="category">{props.dataCat}</span>
                            <h5 className="title">{props.title}</h5>
                        </div>
                    </div>
                </Link>
            </BrowserRouter>
        </div>
    );
}

export default Thumbnail;
