import React, { useEffect } from "react";
import Project from "./Project.js"; // Import the Thumbnail component
import { FaWordpress } from "react-icons/fa";

import TenDegrees from "./../Images/10degrees.png";
import Synairgen from "./../Images/synairgen.png";
import Universum from "./../Images/universum.png";
import LifeIT from "./../Images/life-it.png";
import Tpftf from "./../Images/tpftf.png";
import SmokinBean from "./../Images/smokin-bean.png";

function Projects() {
    const style = {
        fontSize: "0.8rem",
        marginBottom: "50px",
        textAlign: "center",
    };

    useEffect(() => {
        const filterLabels = document.querySelectorAll(
            "label.filter-selector input"
        );
        const filterLabelAll = document.querySelectorAll(
            'label.filter-selector input[value="all"]'
        );
        const filterSelector = document.querySelectorAll(
            "label.filter-selector"
        );
        filterLabelAll.checked = true; // reset onload

        filterLabels.forEach((filter) => {
            filter.addEventListener("click", function (e) {
                const value = this.value;
                const cat = this.getAttribute("data-category");
                const blocks = document.querySelectorAll(".project");

                blocks.forEach((block) => {
                    block.classList.add("hidden");
                    if (block.getAttribute("data-category") === cat) {
                        block.classList.remove("hidden");
                    }

                    if (value === "all") {
                        block.classList.remove("hidden");
                    }
                });

                // active state
                const parent = this.parentElement;
                filterSelector.forEach((selector) => {
                    selector.classList.remove("selected-filter");
                    if (selector === parent) {
                        selector.classList.add("selected-filter");
                    }
                });
            });
        });
    }, []);

    return (
        // Render a Thumbnail component
        <div className="projects">
            <h2>Projects</h2>
            <div className="projects-filter">
                <label className="filter-selector selected-filter">
                    <input
                        name="filter-selector-radio"
                        data-category="all"
                        type="radio"
                        value="all"
                    />
                    All Projects
                </label>
                <label className="filter-selector">
                    <input
                        name="filter-selector-radio"
                        data-category="10 Degrees"
                        type="radio"
                        value="10 Degrees"
                    />
                    Employed at 10 Degrees
                </label>
                <label className="filter-selector">
                    <input
                        name="filter-selector-radio"
                        data-category="Jerram Marketing"
                        type="radio"
                        value="Jerram Marketing"
                    />
                    Employed at Jerram Marketing
                </label>
            </div>
            {/* <p style={style}>
                The following projects were developed while employed at{" "}
                <a
                    href="https://www.10degrees.uk/"
                    target="_blank"
                    rel="noreferrer"
                >
                    10 Degrees
                </a>
                .
            </p> */}
            <div className="content">
                <Project
                    link="https://www.10degrees.uk"
                    image={TenDegrees}
                    title="10 Degrees"
                    dataCat="10 Degrees"
                    icon={FaWordpress}
                />

                <Project
                    link="https://www.covidtrialathome.com"
                    image={Synairgen}
                    title="Synairgen"
                    dataCat="10 Degrees"
                    icon={FaWordpress}
                />

                <Project
                    link="https://universumglobal.com"
                    image={Universum}
                    title="Universum"
                    dataCat="10 Degrees"
                    icon={FaWordpress}
                />

                <Project
                    link="https://lifeit.co.uk/"
                    image={LifeIT}
                    title="Life IT"
                    dataCat="Jerram Marketing"
                    icon={FaWordpress}
                />

                <Project
                    link="https://theprincephiliptrustfund.org/"
                    image={Tpftf}
                    title="The Prince Philip Trust Fund"
                    dataCat="Jerram Marketing"
                    icon={FaWordpress}
                />

                <Project
                    link="https://www.smokinbean.co.uk/"
                    image={SmokinBean}
                    title="Smokin' Bean"
                    dataCat="Jerram Marketing"
                    icon={FaWordpress}
                />
            </div>
        </div>
    );
}

export default Projects;
